::marker {
    content: counters(list-item, '.');
}

.list-item {
    list-style: inside;
    @media screen and (max-width: 500px) {
        padding-left: 2em;
    }
    li {
        @media screen and (max-width: 500px) {
            margin-bottom: 0.67em;
        }
        span {
            padding-left: 2em;
        }
        ul {
            counter-reset: inherit;
        }
    }
}