header {
    display: flex;
    flex-direction: row;
    height: 80pt;
    background: $black-blue;
    flex-shrink: 0;
    align-items: center;
    padding: 0 2em;
    justify-content: space-between;
    &::before {
        background: $white;
    }
    @media screen and (max-width: 500px) {
        .mobilebtn {
            width: 64pt;
            height: 64pt;
            border-radius: 50%;
            border: none;
            color: $white;
            background: $red;
            font-size: 34pt;
            position: relative;
            top: -40px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            z-index: 1;
        }
        margin-top: 2.7em;
        height: 64pt;
        order: 2;
        div {
            display: none;
        }
        nav {
            display: flex;
            flex-direction: row;
            width: 100%;
            justify-content: space-between;
            align-items: center;
            a {
                margin: 1em !important;
                font-size: 10pt !important;
                line-height: 20pt !important;
                font-weight: 400 !important;
            }
        }
    }
    nav {
        flex: 1 0 auto;
        text-align: end;
        a {
            margin: 2em;
            font-size: 16pt;
            line-height: 22pt;
            font-family: "DM Sans", sans-serif;
            font-weight: 700;
            color: rgba(112, 112, 112, 36%) !important;
            &:hover {
                color: $white !important;
            }
            &.active {
                color: $white !important;
            }
            .icon_text {
                content: "";
                @media screen and (max-width: 500px) {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    span {
                        width: 27px;
                        height: 27px;
                    }
                }
            }
        }
    }
}

main {
    @media screen and (max-width: 500px) {
        height: 100vh;
        overflow: auto;
    }
}