@import "../../assets/scss/variables/colors.scss";
.requestbtn {
    width: 360pt;
    height: 52pt;
    color: white;
    text-transform: uppercase;
    font-size: 16pt;
    line-height: 22pt;
    font-weight: bold;
    font-family: "DM Sans", sans-serif;
    background: $red;
    border: none;
    cursor: pointer;
    border-radius: 50pt;
}

.btnrole {
    width: 140pt;
    height: 36pt;
    border: none;
    border-radius: 30pt;
    background: none;
    cursor: pointer;
    font-size: 16pt;
    line-height: 22pt;
    font-weight: 400;
    font-family: "DM Sans", sans-serif;
    color: $black-blue;
    transition: all 0.3s ease-in-out;
}

.active {
    color: $white;
    background: $black-blue;
}

.bluedark {
    color: rgba(255, 255, 255, 48%) !important;
}

.ant-collapse-header {
    color: rgba(1, 34, 58, 48%) !important;
}

input[type=file] {
    color: transparent;
    // width: auto;
    align-items: flex-start;
    &::-webkit-file-upload-button {
        visibility: hidden;
    }
    // &:after {
    //     content: url('../../assets/img/mobileicons/ic_upload.png');
    // }
    &:valid {
        color: unset;
    }
}