.comment-title {
    font-size: 18pt;
    line-height: 24pt;
    font-family: "DM Sans", sans-serif;
    font-weight: 400;
    margin-top: 1.3em !important;
    margin-bottom: 0.75em !important;
}

.comment {
    display: flex;
    flex-direction: column;
    margin-top: 2.1em;
    .comment-header {
        display: flex;
        flex-direction: row;
        .comment-avatar {
            margin-right: 1.2em;
        }
        div {
            h5 {
                color: rgba(1, 34, 58, 48%);
            }
        }
    }
}