@import '../variables/text.scss';
@each $name,
$sizes in $size {
    @each $lineName,
    $lineSize in $line {
        @each $nameWeights,
        $sizeWeights in $weights {
            .#{$name}-line-#{$lineName}-w-#{$nameWeights} {
                font-size: $sizes !important;
                line-height: $lineSize !important;
                font-weight: $sizeWeights !important;
                @if($name==extralarge) {
                    font-family: 'Space Grotesk', sans-serif;
                    @media screen and (max-width: 500px) {
                        font-size: 48pt !important;
                        line-height: 48pt !important;
                        font-weight: 300 !important;
                    }
                    @media screen and (min-width:400px) and (max-width:479px) {
                        font-size: 34pt !important;
                        line-height: 34pt !important;
                    }
                    @media screen and (min-width:320px) and (max-width:399px) {
                        font-size: 22pt !important;
                        line-height: 22pt !important;
                    }
                }
                @if ($name==large) {
                    font-family: 'Space Grotesk', sans-serif;
                    @media screen and (max-width: 500px) {
                        font-size: 48pt !important;
                        line-height: 48pt !important;
                        font-weight: 300 !important;
                    }
                    @media screen and (min-width:400px) and (max-width:479px) {
                        font-size: 39pt !important;
                        line-height: 40pt !important;
                    }
                    @media screen and (min-width:320px) and (max-width:399px) {
                        font-size: 22pt !important;
                        line-height: 22pt !important;
                    }
                }
                @if($name==medium) {
                    font-family: 'Space Grotesk', sans-serif;
                    @media screen and (max-width: 500px) {
                        font-size: 32pt !important;
                        line-height: 38pt !important;
                        font-weight: 300 !important;
                    }
                    @media screen and (min-width:400px) and (max-width:479px) {
                        font-size: 28pt !important;
                        line-height: 20pt !important;
                    }
                    @media screen and (min-width:320px) and (max-width:399px) {
                        font-size: 16pt !important;
                        line-height: 8pt !important;
                    }
                }
                @if($name==normal) {
                    font-family: 'DM Sans', sans-serif;
                    @media screen and (max-width: 500px) {
                        font-size: 16pt !important;
                        line-height: 22pt !important;
                        font-weight: 300 !important;
                    }
                    @media screen and (min-width:400px) and (max-width:479px) {
                        font-size: 16pt !important;
                        line-height: 16pt !important;
                    }
                    @media screen and (min-width:320px) and (max-width:399px) {
                        font-size: 12pt !important;
                        line-height: 14pt !important;
                    }
                }
                @if($name==body) {
                    font-family: 'DM Sans', sans-serif;
                    @media screen and (max-width: 500px) {
                        font-size: 16pt !important;
                        line-height: 22pt !important;
                        font-weight: 300 !important;
                    }
                    @media screen and (min-width: 400px) and (max-width:479px) {
                        font-size: 14pt !important;
                        line-height: 22pt !important;
                    }
                    @media screen and (min-width:320px) and (max-width:399px) {
                        font-size: 8pt !important;
                        line-height: 12pt !important;
                    }
                }
                @if($name==small) {
                    font-family: 'DM Sans', sans-serif;
                }
            }
        }
    }
}

