@import '../../assets/scss/variables/colors.scss';
form {
    @media screen and (max-width: 499px) {
        text-align: center;
    }
}

.group {
    position: relative;
    input {
        width: 480px;
        height: 69px;
        @media screen and (max-width: 499px) {
            width: 100%;
            height: 100%;
            max-width: 480px;
            max-height: 69px;
        }
        box-sizing: border-box;
        border-radius: 40px;
        padding: 30px;
        border: none;
        background-color: rgba(1, 34, 58, 8%);
        color: $black-blue;
        font-size: 16pt !important;
        line-height: 22pt !important;
        &:focus {
            border: 2px solid $black-blue;
            outline: none;
        }
        &:focus~label,
        &:valid~label {
            top: 3px;
            font-size: 12pt;
        }
        cursor: pointer;
    }
    label {
        font-size: 14pt;
        line-height: 18pt;
        font-family: 'DM Sans', sans-serif;
        position: absolute;
        pointer-events: none;
        left: 30px;
        top: 20px;
        transition: 300ms ease all;
    }
}

.padding-half {
    padding-right: 0.3em;
}

.signin-btn {
    width: 360pt;
    height: 52pt !important;
    font-size: 16pt !important;
    line-height: 22pt !important;
    font-weight: bold !important;
    font-family: "DM Sans", sans-serif;
    border-radius: 40px !important;
    text-transform: uppercase;
    background: $red !important;
    color: $white !important;
    &:hover,
    &:focus {
        outline: none !important;
        border: none !important;
    }
    @media screen and (max-width:576px) {
        width: 100%;
    }
}

.padding {
    padding: 0 !important;
}