@import '../../assets/scss/variables/colors.scss';
.ant-picker {
    background: rgba(1, 34, 58, 8%) !important;
    border: none !important;
}

.ant-picker-focused {
    border: 2px solid $black-blue !important;
    outline: none;
    box-shadow: none !important;
}

.ant-picker .ant-picker-borderless {
    background-color: rgba(1, 34, 58, 8%) !important;
}

.ant-picker-panel-container {
    border-radius: 21px !important;
    font-size: 16pt !important;
    line-height: 22pt !important;
    font-family: "DM Sans", sans-serif;
    color: $black-blue;
    .ant-picker-panel {
        width: 100%;
        .ant-picker-date-panel {
            .ant-picker-header {
                .ant-picker-header-super-next-btn {
                    display: none !important;
                }
                .ant-picker-header-super-prev-btn {
                    display: none;
                }
            }
            width: 100%;
            .ant-picker-body {
                width: 100%;
                .ant-picker-content {
                    width: 100%;
                    tbody {
                        tr {
                            .ant-picker-cell-today {
                                .ant-picker-cell-inner {
                                    width: 36pt !important;
                                    height: 36pt !important;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    text-align: center;
                                    &::before {
                                        border-radius: 18pt !important;
                                        border-color: rgba(49, 171, 232, 100%) !important;
                                    }
                                }
                            }
                            td {
                                .ant-picker-cell-inner {
                                    width: 36pt !important;
                                    height: 36pt !important;
                                    border-radius: 18pt !important;
                                    display: flex;
                                    flex-direction: column;
                                    justify-content: center;
                                    text-align: center;
                                }
                            }
                        }
                    }
                }
            }
        }
        .ant-picker-footer {
            display: none;
        }
    }
}

.ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner,
.ant-picker-cell-in-view.ant-picker-cell-range-start .ant-picker-cell-inner,
.ant-picker-cell-in-view.ant-picker-cell-range-end .ant-picker-cell-inner {
    background: rgba(49, 171, 232, 100%) !important;
}

.ant-picker-month-panel {
    width: 100%;
}

.ant-picker-year-panel {
    width: 100%;
}

.ant-picker-input {
    input {
        &::placeholder {
            color: rgba(1, 34, 58, 0.48) !important;
            font-size: 14pt !important;
            line-height: 18pt !important;
        }
    }
}

.dropdown {
    background: url('../../assets/img/icons/ic_arrow_drop.png') no-repeat right 7px 7px;
    padding-left: 30px;
}