@import "../../assets/scss/colors/index.scss";
.ant-select-selector {
    padding-left: 0 !important;
}

.card-deals {
    width: 200pt;
    border-radius: 16pt !important;
    background: rgb(228, 229, 230) !important;
    text-align: left;
}

.ant-card-cover {
    padding: 24px;
}

.iconposition {
    svg {
        width: 21px;
        height: 21px;
    }
}

.searchicondeals {
    svg {
        width: 32px;
        height: 32px;
        cursor: pointer;
        color: #01223A;
    }
}

.ant-collapse-header {
    display: flex;
}