@import '../../assets/scss/variables/fonts.scss';
@import '../../assets/scss/variables/colors.scss';
.circle {
    font-size: 21px;
    border: 3px solid #01223A;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.btn-dark {
    width: 240px;
    height: 69px !important;
    border: 3px solid white !important;
    text-transform: uppercase;
    transition: all 0.3s;
    color: $white !important;
    font-family: 'DM Sans', sans-serif;
    line-height: 20pt !important;
    font-weight: 700 !important;
    font-size: 16pt !important;
    font-weight: 500 !important;
    &:hover {
        background: $white !important;
        border-color: $white !important;
        color: $black-blue !important;
    }
    @media screen and (max-width:500px) {
        font-size: 12pt !important;
        line-height: 22pt !important;
    }
}

.btn-ru {
    font-size: 12pt !important;
    line-height: 22pt !important;
}

.center-li {
    align-items: center !important;
}

.btn-light {
    width: 240px;
    height: 69px !important;
    border: 3px solid $black-blue !important;
    background: none !important;
    color: $black-blue !important;
    text-transform: uppercase;
    transition: all 0.3s;
    font-family: 'DM Sans', sans-serif;
    line-height: 20pt !important;
    font-weight: 700 !important;
    font-size: 16pt;
    font-weight: 500 !important;
    &:hover {
        background: $black-blue !important;
        border-color: $black-blue !important;
        color: $white !important
    }
    @media screen and (max-width:500px) {
        font-size: 12pt !important;
        line-height: 22pt !important;
    }
}

.text-footer {
    font-size: 16pt !important;
    line-height: 22pt !important;
    font-weight: 300 !important;
    font-family: "DM Sans", sans-serif;
}

.res {
    padding-left: 0.3em;
    padding-right: 0.3em;
    letter-spacing: -3px;
    font-weight: 300 !important;
}

.padding-footer {
    padding-top: 1.3em !important;
    padding-bottom: 1.3em !important;
}

.response-text-footer {
    font-size: 22pt !important;
    line-height: 28pt !important;
}

.bordered-box {
    border-radius: 0 105px 0 105px !important;
}

.bordered-box-reverse {
    border-radius: 105px 0 105px 0 !important;
}

.titles {
    font-family: "DM Sans", sans-serif;
    font-size: 22pt !important;
    line-height: 28pt !important;
    font-weight: 300 !important;
    @media screen and (max-width: 420px) {
        font-size: 18pt !important;
        line-height: 20pt !important;
        font-weight: 300 !important;
    }
}

.center-media {
    @media screen and (max-width: 1600px) {
        text-align: center;
        align-items: center;
    }
}

.center-media-footer {
    @media screen and (max-width: 577px) {
        text-align: center !important;
        align-items: center;
    }
}

.display-media-none {
    @media screen and (max-width: 577px) {
        display: none !important;
    }
}

.display-media-screen {
    display: none !important;
    @media screen and (max-width: 577px) {
        display: block !important;
    }
}

.auto {
    margin: auto;
}

.order-2 {
    @media screen and (max-width: 577px) {
        order: 2;
    }
}

.margin-media {
    @media screen and (max-width: 1000px) {
        margin: 1em 0;
    }
}

.text-right {
    text-align: right;
    @media screen and (max-width: 577px) {
        text-align: center;
    }
}

.link-btn>span {
    transition: all 0.3s;
    text-decoration: none;
    &:hover {
        transition: all 0.3s;
        text-decoration: underline;
    }
}

.hover {
    transition: all 0.3s;
}

.hover:hover {
    cursor: pointer;
    .ant-list-item-meta-avatar>div {
        color: $blue !important;
        border: 2px solid $blue !important;
    }
    .ant-list-item-meta-content>h4>h3 {
        color: $blue !important;
    }
}

.border {
    border-radius: 0 25%;
}

.border-screen {
    @media screen and (max-width: 576px) {
        border-radius: 0 100px !important;
    }
}

.selects {
    width: 120px;
    @media screen and (max-width: 576px) {
        width: inherit;
    }
}

.center {
    @media screen and (max-width: 576px) {
        width: 100%;
        justify-content: center;
    }
}

.texts {
    text-align: left;
    width: 75%;
    @media screen and (max-width: 576px) {
        text-align: center;
        width: 75%;
    }
}

.unvisible {
    @media screen and (max-width: 576px) {
        display: none;
    }
}

.aligment {
    text-align: center;
    @media screen and (max-width: 576px) {
        text-align: center;
    }
}

.title-response {
    text-align: left;
    @media screen and (max-width: 576px) {
        padding-right: 0em;
        text-align: left;
        font-weight: 500 !important;
        line-height: 64px !important;
    }
}

.footer-response {
    @media screen and (max-width: 576px) {
        font-size: 64px !important;
    }
}

.responsewidth {
    @media screen and (max-width: 576px) {
        width: 100% !important;
    }
}

