@import '../../assets/scss/variables/colors.scss';
.writeusbtn {
    width: 235pt;
    height: 52pt;
    border: none;
    background: $white;
    border: 3px solid $black-blue;
    color: $black-blue;
    text-transform: uppercase;
    font-size: 16pt;
    line-height: 22pt;
    border-radius: 40pt;
    font-weight: 500;
    margin-left: 21px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
        background: $black-blue;
        color: $white;
    }
}

.panelcolapse {
    div {
        flex-direction: column;
        div {
            font-size: 12pt;
            line-height: 18pt;
            font-family: "DM Sans", sans-serif;
            font-weight: 400;
        }
    }
}